@import '../../marketplace.css';

.root {
  & p {
    @apply --marketplaceH4FontStyles;
    text-align: inherit;
  }
  & h2 {
    /* Adjust heading margins to work with the reduced body font size */
    margin: 23px 0 19px 0;
    padding-top: 25px;

    @media (--viewportMedium) {
      margin: 24px 0 24px 0;
    }
  }
}

.postContainer {
  border: 1px solid #ff3f00;
  border-radius: 4px;
  overflow-y: auto;
  margin-top: 25px;
}

.postTitle {
  padding: 0px 0px 15px 0px;
  margin: 0px;
    font-weight: bold;
    font-size: 200%;
    color: #ff3f00;
}

.readMore {
    font-weight: bold;
    font-size: 90%;
    text-transform: uppercase;
}

.readMore a {
    color: #000;
    text-transform: uppercase;
    text-decoration: none;
}

.readMore a:hover {
    color: #ff3f00;
    text-decoration: underline;
}


.image {
  border-radius: 0px; 
  padding-bottom:35%;
  background-repeat:no-repeat;
  background-size:cover;
  background-position:center center;
  position:center;
  width:50%;
}

.desc, .image_left {
  float:left;
  display:block
}

.imageRight {
  float:right;
  display:block
}

.desc {
  width:50%;
  text-align:left;
  padding:15px 15px 15px 15px;
  color: #424242;
}

@media (max-width:600px) {
  .desc, .image {
    float:none;
    width:100%;
  }
}  

h1 {
    color: #ff3f00;
}